ion-content {
    // font-family: pop-medium;
    font-family: "mon-bold";
    .toolbar {
        background-color: var(--theme-blue-color);
        align-items: center;
        align-content: center;
        color: white;
        text-align: center;
        justify-content: center;
        align-self: center;
        padding: 40px;
        font-family: "mon-bold";
        font-size: 19px;
        text-transform: uppercase;
    }

    .container {
        // background-color: white;
        background-color: #121212;
        // color: white;
        // color: #121212;
        border-top-left-radius: 32px;
        padding: 20px;
        border-top-right-radius: 32px;
        margin-top: -25px;
        // align-items: center;

        ion-item {
            --inner-padding-end: 0px;
            --background: transparent;
            --color: black;
            ion-label {
                font-family: "mon-medium";
                color: var(--black-color);
                color: var(--white-color);
                font-size: 21px;
            }
            ion-input {
                color: black !important;
                font-family: "mon-medium";
                font-size: 16px;
                border: 1px solid #c3c3c3;
                border-radius: 20px;
                margin-top: 14px;
                --padding-top: 12px;
                --padding-bottom: 12px;
                --padding-start: 12px;
                --padding-end: 12px;
            }
        }
        .btn {
            width: 100%;
            border-radius: 23px;
            --border-radius: 23px;
            font-family: "mon-bold";
            // color: white;
            color: var(--ion-color-primary-contrast);
            --background: var(--theme-blue-color);
            height: 50px;
            overflow: hidden;
            margin-top: 13px;
            text-transform: capitalize;
            font-size: 16px;
            margin-bottom: 8px;
        }
        .input-div {
            margin-top: 25px;
        }
        .badge {
            margin-bottom: 30px;
        }
    }
    .ra {
        --background: transparent;
        padding: 0;
        margin: 0;
        --padding-top: 0px;
        --inner-padding-top: 0;
        --inner-padding-end: 0;
        --inner-padding-bottom: 0;
        --inner-padding-start: 0;
        --padding-bottom: 0;
        --padding-start: 0;
        --padding-end: 0;
        ion-label {
            font-family: "mon-medium";
            font-size: 17px;
            color: var(--black-color);
        }
    }
    .back {
        position: absolute;
        left: 0;
        top: 29px;
        font-family: "mon-bold";
        font-size: 29px;
    }
    .logout {
        position: absolute;
        right: 0;
        top: 29px;
        font-family: "mon-bold";
        font-size: 29px;
    }
    .error {
        font-family: "mon-medium";
        color: red;
        font-size: 15px;
        padding-right: 5px;
    }
    .mandatoryField {
        color: red;
        font-size: 12px;
        margin-bottom: 100px;
    }
    .transparent-modal {
        .modal-wrapper {
            --background: rgba(44, 39, 45, 0.2);
        }
    }
    .uploadFile {
        margin-top: 10px;
    }
    .question {
        font-size: 16px !important;
        font-family: "mon-medium";
    }
    .value {
        font-family: "mon-medium";
        font-size: 14px !important;
    }
}

ion-card {
    padding: 16px;
    border-radius: 19px;
    margin: 15px;
    font-family: "mon-medium";
    font-size: 16px !important;
    background: white;
    color: black !important;
    ion-card-header ion-card-title {
        color: black;
    }
}

ion-radio {
    --color: var(--theme-blue-color);
}

ion-select {
    color: black;
    width: 100%;
    border-radius: 20px;
    --padding-start: 15px;
    --padding-end: 15px;
}

ion-datetime {
    color: black;
}

ion-input {
    color: black !important;
}

.alert-message {
    color: red !important;
    font-size: 18px !important;
}

.preview-slides img {
    height: 200px;
    width: 300px;
}

.progress-bar {
    padding-top: 20px;
    padding-bottom: 20px;
}

ion-item-sliding {
    background: white;
    color: black !important;
}

